.images__wrapper {
    display: grid;
    grid-template-columns: 130px 130px 130px 130px;
    grid-gap: 140px 170px;
    margin-top: 80px;
    padding-right: 100px;
}

.image {
    width: 250px;
    height: 250px;
}

.image img {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    object-fit: cover;
    background-position: center;
    box-shadow: 0 12px 10px -6px rgba(0, 0, 0, 0.3);
    cursor: pointer;
    transition: all;
}

.image img:hover {
    box-shadow: none;
    transition-duration: 0.3s;
    transform: translateY(5px);
  }